#loading-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99999;
  transition: all 0.5s ease-in-out;
  background-color: #ffffff;
}
#loading-container .loading-wrapper {
  position: absolute;
  padding-bottom: 75px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 450px;
  padding: 0 20px;
}
#loading-container #loading-logo {
  text-align: center;
  padding: 20px 0;
}
#loading-container #loading-logo img,
#loading-container #loading-logo svg {
  max-width: 650px;
  width: 100%;
}
#loading-container #loading-logo img .path,
#loading-container #loading-logo svg .path {
  stroke-dasharray: 1350;
  stroke-dashoffset: 1350;
  stroke-width: 1px;
  stroke: rgba(241, 102, 127, 0);
  fill: rgba(255, 255, 255, 0);
}
#loading-container .progress {
  height: 15px;
  background-color: #e9ecef;
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 3px black;
  border-radius: 8px;
  overflow: hidden;
}
#loading-container #progress-bar {
  max-width: 100%;
  width: 0%;
  height: 100%;
  background-color: #ff9e0c;
  background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.3) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.3) 50%, rgba(255, 255, 255, 0.3) 75%, transparent 75%, transparent);
  background-size: 1rem 1rem;
  background-repeat: repeat;
  animation: background 10s linear infinite forwards;
}
#loading-container .progress-status {
  margin-top: 15px;
  text-align: center;
  font-size: 16px;
  letter-spacing: 3px;
  font-weight: 600;
}
#loading-container.loading #loading-logo svg .path-1 {
  animation: stroke 0.8s linear forwards, fill 0.8s 0.8s linear forwards;
}
#loading-container.loading #loading-logo svg .path-2 {
  animation: stroke 0.8s 0.8s linear forwards, fill 0.8s 1.6s linear forwards;
}
#loading-container.closing {
  opacity: 0;
}
#loading-container.closing .loading-wrapper {
  transition: all 0.3s ease;
  transform: translate(-50%, -50%) scale(0.9);
}

